import { Component, Inject, OnInit } from '@angular/core';
import { ITratamiento } from '../../models/tratamiento';
import { TratamientosService } from '../../service/tratamientos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
@Component({
    selector: 'app-save-tratamiento',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    tratamiento: ITratamiento;
    title: string;
    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _tratamientosService: TratamientosService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>
        ) {
        this.tratamiento = this.data ? this.data : null;
        this.title = this.tratamiento?.idtratamiento ? 'Editar tratamiento' : 'Nuevo tratamiento';
    }

    ngOnInit(): void {
        this.newForm();
    }

    newForm() {
        this.form = new FormGroup({
            nombre: new FormControl((this.tratamiento ? this.tratamiento.descripcion : ''), Validators.required),
            activo: new FormControl((this.tratamiento ? (this.tratamiento.activo ? 1 : 0) : 0)),
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.tratamiento = {
                ...this.tratamiento,
                descripcion: this.form.get('nombre').value,
                activo: this.form.get('activo').value,
            }
            if (this.tratamiento.idtratamiento) {
                this._tratamientosService.updateTratamiento(this.tratamiento)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            } else {
                this._tratamientosService.saveTratamiento(this.tratamiento)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}